import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import LocationTypeCreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/companyMaster/locationType/actions";
import { Box } from "../../../App/app.style";

const redirectTo = '/dashboard/companies/location-types'

class CreateLocationType extends Component {
  // Get All data diawal
  componentDidMount() {
    const { createLocationType } = this.props;
    createLocationType();
  }

  componentDidUpdate() {
    // Display error notif
    if (this.props.locationType.error) {
      notification("error", this.props.locationType.error);
      this.props.clearError();
    }

    // Display success notif
    if (this.props.locationType.success) {
      this.props.history.replace(redirectTo);
    }
  }


  handleSubmit = value => {
    const { storeLocationType } = this.props;
    storeLocationType(value);
  };

  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader title={<IntlMessages id="locationTypes.add.title" />}
            style={{ margin: 0 }}
          >
            <Button onClick={() => this.props.history.replace(redirectTo)}>
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <LocationTypeCreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            locationType={this.props.locationType}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return { auth: state.auth, locationType: state.locationType };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateLocationType);
