import React, { Component } from "react";

import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import { Form, Input, Divider, Col, Checkbox } from "antd";

//const { Option } = Select;
const { TextArea } = Input;
const InputGroup = Input.Group;

class LocationTypeCreateForm extends Component {
  state = {
    check_code: false
  };

  handleChangeCode = () => {
    const { setFieldsValue } = this.props.form;
    this.setState({ check_code: !this.state.check_code });
    if (this.state.check_code === false) {
      setFieldsValue({
        ["location_type_code"]: null
      });
    }
  };

  handleSubmit = e => {
    e.preventDefault();
    this.props.form.validateFieldsAndScroll((err, values) => {
      if (!err) {
        this.props.submit(values);
      }
    });
  };

  render() {
    //console.log(this.props.locationType);
    const { getFieldDecorator, getFieldError } = this.props.form;
    const { locationType } = this.props;
    const { loading } = locationType;
    const error = locationType.errorField ? locationType.errorField : [];
    // console.log(data);

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const formTextArea = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 24 }
      }
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0
        },
        sm: {
          span: 24,
          offset: 0
        }
      }
    };

    return (
      <Form layout="vertical" onSubmit={this.handleSubmit}>
        {/* <Divider orientation="left" style={{ fontSize: 14 }}>
          <IntlMessages id="locationTypes.add.subtitle.data" />
        </Divider> */}
        <div className="isoDetailDataFlex" style={{ padding: "0" }}>
          <div
            className="isoColumn-2 isoFormGroupComp"
            style={{ padding: "0 15px 0 0" }}
          >
            <InputGroup compact>
              <Col span={20}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="locationTypes.add.code" />}
                  validateStatus={
                    error.location_type_code ||
                    getFieldError("location_type_code")
                      ? "error"
                      : null
                  }
                  help={
                    error.location_type_code
                      ? error.location_type_code
                      : getFieldError("location_type_code")
                  }
                >
                  {getFieldDecorator("location_type_code", {
                    rules: [
                      {
                        max: 30,
                        message: <IntlMessages id="maxLength30" />
                      }
                    ]
                  })(<Input disabled={this.state.check_code ? true : false} />)}
                </Form.Item>
              </Col>
              <Col span={4}>
                <Form.Item
                  {...formItemLayout}
                  label={<IntlMessages id="autoGenerateCode" />}
                >
                  <Checkbox
                    style={{
                      paddingTop: "5px",
                      paddingLeft: "8px"
                    }}
                    checked={this.state.check_code}
                    onChange={this.handleChangeCode}
                  />
                </Form.Item>
              </Col>
            </InputGroup>

            <Form.Item
              {...formItemLayout}
              label={<IntlMessages id="locationTypes.add.name" />}
            >
              {getFieldDecorator("location_type_name", {
                rules: [
                  {
                    required: true,
                    message: <IntlMessages id="required" />
                  },
                  {
                    max: 50,
                    message: <IntlMessages id="maxLength50" />
                  }
                ]
              })(
                <Input
                //autosize={{ minRows: 2 }}
                //disabled={used ? true : false}
                />
              )}
            </Form.Item>

            <Form.Item
              {...formTextArea}
              label={<IntlMessages id="locations.add.description" />}
            >
              {getFieldDecorator("description", {
                // rules: [
                //   {
                //     required: true,
                //     message: <IntlMessages id="required" />
                //   }
                // ]
              })(
                <TextArea
                  autosize={{ minRows: 2 }}
                  //disabled={used ? true : false}
                />
              )}
            </Form.Item>
          </div>
        </div>

        <Form.Item {...tailFormItemLayout}>
          <Button
            type="primary"
            htmlType="submit"
            style={{ marginTop: "20px", marginLeft: "50px" }}
            loading={loading}
          >
            <IntlMessages id="button.submit" />
          </Button>
        </Form.Item>
      </Form>
    );
  }
}

const WrappedLocationTypeCreateForm = Form.create({
  name: "create_location_type"
})(LocationTypeCreateForm);

export default WrappedLocationTypeCreateForm;
